<template>
    <div>
        <basic-header :items="['','']"></basic-header>
        <el-card>
            <el-tabs v-model="activeName" @tab-click="handleTabClick">
                <el-tab-pane label="" name="first">
                    <!---->
                    <el-form :inline="true" class="demo-form-inline">
                        <el-form-item label="">
                            <el-input  :clearable="true"  v-model="searchForm.name"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select placeholder="" :clearable="true" v-model="searchForm.store">
                                <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item>
                            <el-date-picker
                                    v-model="searchForm.date"
                                    type="datetimerange"
                                    range-separator=""
                                    start-placeholder=""
                                    end-placeholder="">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="search" icon="el-icon-search" circle></el-button>
                        </el-form-item>
                    </el-form>
                    <!---->

                        <el-table :data="tableData" border style="width: 100%">
                            <el-table-column prop="id" label="ID"></el-table-column>
                            <el-table-column prop="name" label=""></el-table-column>
                            <el-table-column prop="catname" label=""></el-table-column>
                            <el-table-column prop="description" label=""></el-table-column>
                            <el-table-column prop="thumb" label="">
                                <template #default="o">
                                    <img src="o.row.thumb" alt="" height="100px">
                                </template>
                            </el-table-column>
                            <el-table-column prop="price" label=""></el-table-column>
                            <el-table-column prop="store" label=""></el-table-column>
                            <el-table-column prop="sort" label=""></el-table-column>
                            <el-table-column prop="create_time" label="">
                                <template #default="o">{{o.row.create_time | formatDate}}</template>
                            </el-table-column>
                            <el-table-column prop="update_time" label="">
                                <template #default="o">{{o.row.update_time | formatDate}}</template>
                            </el-table-column>
                            <el-table-column prop="state" label="">
                                <template #default="o">
                                    <div v-if="o.row.state===1"></div>
                                    <div v-else></div>
                                </template>
                            </el-table-column>
                            <el-table-column prop="operating" label="">
                                <template #default="o">
                                    <el-button type="text" @click="edit(o.row.id)"></el-button>
                                    <el-button type="text" @click="del(o.row.id)"></el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <!---->
                        <el-pagination
                                background
                                style="margin-top: 30px"
                                layout="prev, pager, next"
                                :current-page.sync="page"
                                :page-size.sync="pageSize"
                                :total="total"
                                @current-change="change"
                        >
                        </el-pagination>
                </el-tab-pane>
                <!--2-->
                <el-tab-pane label="" name="second">
                    <el-steps :active="active" finish-status="success">
                        <el-step title=""></el-step>
                        <el-step title=""></el-step>
                        <el-step title=""></el-step>
                    </el-steps>
                    <el-form :model="submitForm" :rules="submitRules" ref="submitForm">
                        <el-row>
                            <el-col v-if="active===0" :span="12">
                                <el-form-item label="">
                                    <el-select v-model="submitForm.tid" :clearable="true">
                                        <el-option
                                           v-for="item in typeOption"
                                           :key="item.id"
                                           :label="item.id"
                                           :value="item.name"></el-option>
                                    </el-select>
                                    <router-link :to="{name:'type'}"></router-link>
                                </el-form-item>
                                <el-form-item label="" prop="name">
                                    <el-input v-model="submitForm.name"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="description">
                                    <el-input v-model="submitForm.description"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="price">
                                    <el-input type="number" v-model="submitForm.price"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="weight">
                                    <el-input type="number" v-model="submitForm.weight"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="store">
                                    <el-input type="number" v-model="submitForm.store"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="sort">
                                    <el-input type="number" v-model="submitForm.sort"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="thumb">
                                    <el-upload
                                            class="avatar-uploader"
                                            action="uploadURL"
                                            :show-file-list="false"
                                            :on-success="handleAvatarSuccess"
                                            :before-upload="beforeAvatarUpload">
                                        <img v-if="submitForm.thumb" :src="submitForm.thumb" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                    </el-upload>
                                </el-form-item>
                                <el-form-item label="banner" prop="banner">
                                    <el-upload
                                            class="upload-demo"
                                            action="uploadURL"
                                            :before-upload="beforeAvatarUpload"
                                            :on-success="handleBannerSuccess"
                                            :on-remove="handleBannerRemove"
                                            list-type="picture"
                                            ref="banner"
                                    >
                                        <el-button size="small" type="primary"></el-button>
                                        <div slot="tip" class="el-upload__tip">jpg500kb</div>
                                    </el-upload>
                                </el-form-item>
                            </el-col>
                            <el-col v-if="active===1">
                                <div ref="editor"></div>
                            </el-col>
                            <el-col v-if="active===2">
                                <el-form-item label="" prop="delivery_id">
                                    <el-select v-model="submitForm.delivery_id" :clearable="true">
                                        <el-option
                                                v-for="item in deliveryOption"
                                                :key="item.id"
                                                :value="item.id"
                                                :label="item.name">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-switch
                                        v-model="submitForm.state"
                                        active-color="#13ce66"
                                        inactive-color="#ff4949">
                                    </el-switch>
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="primary" @click="handleSubmit"></el-button>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                    <el-button-group>
                        <el-button type="primary" icon="el-icon-arrow-left" :disabled="active==0" @click="active--"></el-button>
                        <el-button type="primary" :disabled="active==2" @click="active++"><i class="el-icon-arrow-right el-icon--right"></i></el-button>
                    </el-button-group>
                </el-tab-pane>
            </el-tabs>
        </el-card>
        <!---->
        <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="80%">
            <el-form :model="editForm" label-width="100px" :rules="submitRules" ref="editForm">
                <el-row>
                    <el-col  :span="8">
                        <el-form-item label="" prop="tid">
                            <el-select v-model="editForm.tid" :clearable="true">
                                <el-option v-for="item in typeOption" :key="item.id" :value="item.id"
                                           :label="item.name"></el-option>
                            </el-select>
                            <!--                            <router-link :to="{name:'type'}"></router-link>-->
                        </el-form-item>
                        <el-form-item label="" prop="name">
                            <el-input v-model="editForm.name"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="description">
                            <el-input v-model="editForm.description"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="price">
                            <el-input type="number" v-model="editForm.price"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="weight">
                            <el-input type="number" v-model="editForm.weight" placeholder="kg"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="store">
                            <el-input type="number" v-model="editForm.store"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="sort">
                            <el-input type="number" v-model="editForm.sort"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8" :offset="6">
                        <el-form-item label="" prop="thumb">
                            <el-upload
                                    class="avatar-uploader"
                                    action="/api/upload/upload"
                                    :show-file-list="false"
                                    :on-success="handleAvatarSuccess1"
                                    :before-upload="beforeAvatarUpload">
                                <img v-if="editForm.thumb" :src="editForm.thumb" class="avatar">
                            </el-upload>
                        </el-form-item>
                        <el-form-item label="banner" prop="banner">
                            <el-upload
                                    class="upload-demo"
                                    action="/api/upload/upload"
                                    list-type="picture"
                                    :before-upload="beforeAvatarUpload"
                                    :on-success="handleBannerSuccess1"
                                    :on-remove="handleBannerRemove1"
                                    :file-list="fileList"
                                    ref="banner">
                                <el-button size="small" type="primary"></el-button>
                                <div slot="tip" class="el-upload__tip">jpg500kb</div>
                            </el-upload>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item prop="content">
                            <div ref="editor2"></div>
                        </el-form-item>
                    </el-col>
                    <el-col>
                        <el-form-item label="" prop="delivery_id">
                            <el-select v-model="editForm.delivery_id" :clearable="true">
                                <el-option v-for="item in deliveryOption" :key="item.id" :value="item.id"
                                           :label="item.name"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-switch
                                    v-model="editForm.state"
                                    active-color="#13ce66"
                                    inactive-color="#ff4949"
                                    :active-value="1"
                                    :inactive-value="0">
                            </el-switch>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleUpdate"> </el-button>
            </span>
        </el-dialog>
     </div>
</template>

<script>
    import Header from "../../components/Header";
    import wangeditor from "wangeditor"
    import formatDate from "../../utils/formatDate";
    export default {

        data() {

            return {
                api:"/api/goods/goods",
                activeName:"first",
                tableData:[],
                page:1,
                pageSize:5,
                total:10,
                active:0,

                options: [
                    {value: '0', label: ''},
                    {value: '1', label: ''}
                ],
                searchForm:{
                    name:'',
                    store:'',
                    date:''
                },
                typeOption:[],
                typeForm:{
                    typeOption:"",

                },
                submitForm:{
                    tid:"",
                    name:"",
                    description:"",
                    price:"",
                    sort:"",
                    thumb:"",
                    banner:"",
                    store:"",
                    content:"",
                    delivery_id:"",
                    weight:""
                },
                submitRules:{
                    tid:{required: true,message: "",trigger: "blur"},
                    name:{required: true,message: "",trigger: "blur"},
                    description:{required: true,message: "",trigger: "blur"},
                    price:{required: true,message: "",trigger: "blur"},
                    sort:{required: true,message: "",trigger: "blur"},
                    thumb: {required: true, message: '', trigger: "blur"},
                    banner: {required: true, message: 'banner', trigger: "blur"},
                    store:{required: true,message: "",trigger: "blur"},
                    content:{required: true,message: "",trigger: "blur"},
                    delivery_id:{required: true,message: "",trigger: "blur"},
                    weight:{required: true,message: "",trigger: "blur"}
                },
                uploadURL:"/api/upload/upload",
                editor:"",
                deliveryOption:[],
            }
        },
        methods: {
            fetchData(){
                let params={
                    page:this.page,
                    pageSize:this.pageSize,
                }
                if(this.searchForm.name){
                    params['name']=this.searchForm.name;
                }if (this.searchForm.store){
                    params['store']=this.searchForm.store;
                }if (this.searchForm.date){
                    //
                    params["start_time"]=Math.floor(this.searchForm.date[0].getTime()/1000);
                    params["end_time"]=Math.floor(this.searchForm.date[0].getTime()/1000);
                }
                // 
                this.axios.get(this.api,{
                        params
                }).then(res=>{

                    this.tableData=res.data.data;
                    this.total=res.data.total;

                });

            },
            del(id){
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete(this.api, {
                        params: {id}
                    }).then(res => {
                        if (res.data.code === 200) {
                            this.$message.success("");
                            this.fetchData();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            change(){
                this.fetchData()
            },
            search(){
                this.fetchData()
            },
            //
            next() {
                if (this.active++ > 2) this.active = 0;
            },
            //
            getAllType(){
                this.axios.get("/api/type/type").then(res=>{
                    if (res.data.code===200){
                        this.typeOption=res.data.data;
                    }
                })
            },
            getAllDelivery(){
                this.axios.get("/api/delivery/delivery").then(res=>{
                    if (res.data.code===200){
                        this.deliveryOption=res.data.data;
                    }
                })
            },
            handleTabClick(){
                if (this.activeName==="second"){
                    this.getAllType();
                }
            },
            //

            //
            handleAvatarSuccess(res, file) {
                this.form.thumb = res;
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt500k = file.size / 1024 < 500;

                if (!isJPG) {
                    this.$message.error(' JPG !');
                }
                if (!isLt500k) {
                    this.$message.error(' 500kb!');
                }
                return isJPG && isLt500k;
            },
            //banner
            handleBannerSuccess(res, file,fileList) {
                this.submitForm.banner=fileList.map(v=>v.response).join(";");
                this.$refs.submitForm.validateField("banner");
            },
            //
            handleBannerRemove(file,fileList) {
                this.submitForm.banner=fileList.map(v=>v.response).join(";");
            },
            //
            handleSubmit(){

            }


        },
        components:{
            "basic-header":Header
        },
        watch:{
            active(value){
                if(value===1&&this.editor===""){
                    this.$nextTick(()=>{
                        this.editor=new wangeditor(this.$refs.editor);
                        this.editor.customConfig.uploadImgServer='/api/upload/editorupload';
                        this.editor.customConfig.uploadFileName='file';
                        this.editor.create();
                    });
                }
            }
        },
        created() {
            this.fetchData()
        },
        mounted() {

        }
    }
</script>

<style scoped>
    .avatar-uploader .el-upload {
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        border: 1px dashed #d9d9d9;
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }
    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
</style>